    width: -webkit-max-content;
    width: max-content;
    padding: 14px;
    color: white;
    font-size: 14px;
    border-radius: 5px;
    font-family: Myriad Pro;
}

.modal {
    font-family: Roboto;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
