@font-face {
    font-family: "VAG Rounded Std";
    src: url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.eot");
    src: url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.eot?#iefix") format("embedded-opentype"),
    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.woff2") format("woff2"),
    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.ttf") format("truetype"),
    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.svg#VAG Rounded Std") format("svg");
}

@font-face {
    font-family: "Myriad Pro";
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot");
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot?#iefix")
    format("embedded-opentype"), url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff2")
    format("woff2"), url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.ttf") format("truetype"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.svg#Myriad Pro") format("svg");
}

footer {
    color: white;
    font-size: small;
    background-color: #010F3E;
    padding: 25px 1rem;
    margin-top: 300px;
    font-family: Roboto;
}

.footerContainerOne {
    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-direction: column;
    margin-top: 3rem;
}

.insideFooterContainerOne {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    margin-bottom: 2rem;
    margin-left: -1rem;
    justify-content: start;
}

.regulatory-footer {
    margin-bottom: -2rem;
}

.social-link {
    color: #4089BE;
    font-size: large;
    margin-right: 10px;
    margin-left: 78px;
}

.footer-nav-links {
    display: flex;
    flex-direction: row;
    flex-flow: row;
    align-content: space-between;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.footer-nav-link {
    display: list-item;
    list-style-type: circle;
    list-style-position: inside;
}

.footer-navex-logo div {
    margin: 20px;
    justify-content: space-between;
}

.footer-navex-logo button {
    background-color: #4089BE;
}

.insideFooterContainerTwo {
    margin-top: -2rem;
}

.insideFooterContainerThree {
    margin-left: 0;
    font-family: VAG Rounded Std;
}

.email-logo {
    color: #4089BE;
    font-size: large;
    margin: 10px;
}

.email-logo span {
    color: white;
    font-size: small;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
    margin-left: 10px;
    margin-bottom: 10px;
}

.report-button {
    width: 100%;
    margin: 2rem 1rem 1rem;
}

.regulatory-footer img {
    width: 100%;
}

.borrow-caution h3 {
    font-family: VAG Rounded Std;
}

.borrow-caution{
    margin-top: -4rem;
    align-content: space-between;
}

@media (min-width: 992px) {

    footer {
        padding-left: 5rem;
    }

    section {
        margin: 25px 0 25px 100px;
        width: 60%;
        /* border: red 2px solid; */
    }

    section .squre {
        background-color: #17B1F0;
        margin: 0 0 5px 0;
        width: 25px;
        height: 25px;
    }

    section h5 {
        margin: 0 0 10px 0;
        color: #010F3E;
    }

    section h1 {
        margin: 0 0 10px 0;
        color: #010F3E;
    }

    section p {
        margin: 0 0 10px 0;
        font-size: small
    }

    .regulatory-footer {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 1rem;

    }

    .borrow-caution {
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: start;
        text-align: start;
        margin-top: 0;
    }

    .borrow-caution h3{
        font-size: large;
    }

    .borrow-caution span{
        width: 25rem;
        font-size: medium;
        font-family: Myriad Pro;

    }

    footer {
        color: white;
        font-size: small;
        background-color: #010F3E;
        padding: 25px 100px 25px 100px;
        margin-top: 300px;
        font-family: Roboto;
    }

    footer .footerContainerOne {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: -4rem;

    }

    footer .footerContainerTwo p {
        margin: 25px 0 0 0;
    }

    footer .footerContainerTwo p a {
        margin: 25px 0 0 0;
        color: #4089BE;
    }

    .footer-logo {
        margin-left: -10px;
        margin-right: 20px;
    }

    .fab {
        color: #4089BE;
    }

    .social-link {
        color: #4089BE;
        font-size: large;
        margin-right: 10px;
        margin-left: 10px;
        position: relative;
    }

    .insideFooterContainerTwo {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 0;
    }

    .insideFooterContainerThree {
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: start;
        text-align: start;
        padding-left: 5rem;
    }

    .insideFooterContainerThree h3 {
        font-size: large;
    }

    .insideFooterContainerThree span {
        width: 25rem;
        font-size: medium;
        font-family: Myriad Pro;
    }

    .footer-links {
        display: flex;
        flex-direction: column;
        flex-flow: column;
        list-style-type: disc;
        list-style-position: inside;
        align-self: start;
        font-size: medium;
        margin: 0px;
    }

    .insideFooterContainerOne {
        display: flex;
        flex-direction: column-reverse;
    }

    .fas {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-bottom: 10px;
        justify-content: space-between;
    }

    .footer-links:hover {
        cursor: pointer;
    }

    .footer-navex-logo {
        display: inherit;
        flex-direction: column;
    }

    .footerContainerTwo {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .email-logo {
        margin: 0;
    }

    .email-logo span {
        margin-left: -15rem;
        margin-bottom: 0;
    }

    .insideFooterContainerFour {
        display: flex;
        flex-flow: column;
        width: 12rem;
        margin-right: 4.5rem;
    }

    .report-button {
        width: 20%;
    }

    .footer-nav-links {
        display: flex;
        flex-flow: column;
        margin-bottom: 1rem;
    }

    .footer-nav-link {
        display: list-item;
        list-style-type: circle;
        list-style-position: inside;
    }

    .email-icon {
        display: inherit;
        flex-flow: row;
        justify-content: space-evenly;
    }

}