@font-face {
    font-family: Vag Rounded;
    src: url(//db.onlinewebfonts.com/c/50caebd3d1f303be2ec212f78f8c084e?family=VAG+Rounded+Std);
}


@font-face {
    font-family: "Myriad Pro";
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot");
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot?#iefix")
    format("embedded-opentype"), url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff2")
    format("woff2"), url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.ttf") format("truetype"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.svg#Myriad Pro") format("svg");
}

.landing-container {
    width: 100%;
    font-family: Roboto;
    font-size: small;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto -20rem auto;
    padding: 0 0 5rem 0;
}

.landing-intro {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 90%;
    font-family: Roboto;
}

.landing-intro p {
    margin-top: 20px;
}

.landing-intro h1 {
    background-color: #010f3e;
    padding: 10px;
    color: white;
    font-size: medium;
}

.steps h2 {
    background-color: white;
    padding: 10px;
    color: #00a6d6;
    font-size: medium;
}

.landing-subtitle {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    width: 90%;
    margin-top: 1rem;
    font-size: x-small;
    font-family: Myriad Pro, Bold;
    color: #010f3e;
}

.steps {
    margin-left: 20px;
    font-family: Myriad Pro;
}

.steps h2 {
    font-size: small;
}

.landing-container .subtitle {
    display: flex;
    justify-content: flex-start;
    margin-left: 220px;

}

.landing-container .subtitle h1 {
    margin-left: 5px;
    padding-bottom: -10px;
    margin-bottom: -10px;

}

.steps ul {
    list-style-type: circle;
}

.home-section {
    display: flex;
    flex-direction: column;

}

@media (min-width: 992px) {
    .landing-container {
        width: 100vw;
        height: 45vw;
        font-size: medium;
        padding: 0 10rem 10rem 10rem;
    }

    .landing-intro {
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: 90%;
        margin-top: 2%;
        font-family: Roboto, monospace;
    }

    .landing-intro p {
        margin-top: 20px;
    }

    .landing-intro h1 {
        background-color: #010f3e;
        color: white;
        font-size: large;
    }

    .steps {
        margin-left: 20px;
    }

    .landing-subtitle {
        margin-top: 3%;
        font-size: x-large;

    }

    .landing-container .subtitle {
        display: flex;
        justify-content: flex-start;
        margin-left: 220px;

    }

    .landing-container .subtitle h1 {
        margin-left: 5px;
        padding-bottom: -10px;
        margin-bottom: -10px;
        font-size: x-large;
    }

    .steps h2 {
        font-size: medium;
    }
}


