    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.woff2") format("woff2"),
    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.ttf") format("truetype"),
    url("//db.onlinewebfonts.com/t/50caebd3d1f303be2ec212f78f8c084e.svg#VAG Rounded Std") format("svg");
}

@font-face {
    font-family: "Myriad Pro";
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot");
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot?#iefix")
    format("embedded-opentype"), url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff2")
    format("woff2"), url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.ttf") format("truetype"),
